import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
components: {uniPopup},
  data () {
    return {
      swiperList: [],
	  listArr:[
		  {label:'全部'},
		  {label:'潮流时尚'},
		  {label:'婚礼爱情'},
		  {label:'商业宣传'},
		  {label:'家庭相册'},
		  {label:'生日祝福'},
		  {label:'节日问候'},
		  {label:'旅行时光'},
		  {label:'晚会典礼'},
		  {label:'毕业留念'},
		  {label:'通用'}
	  ],
	  videoListArr:[],
	  // 弹出层内容 
	  dialogContent:'',
	  page:1,
	  // 请求基地址
	  baseUrl : getApp().globalData.baseUrl
    }
  },
  onLoad () {
   this.onloadFunc()
  },
  // 上拉触底事件
  onReachBottom() {
	  this.page++
	  this.getVideoList()
	  
  },
  methods: {
	onloadFunc() {
	  // 获取轮播图图片
	  
		uni.request({
		  url: this.baseUrl + '/api/activity',
		  success: res => {
		    if (res.data.status === '1') {
		      this.swiperList = res.data.data.list
		    }
		  },
		  fail: err => {
		    console.log(err);
			this.dialogContent = '请求异常，请稍后重试'
				this.$refs.popup.open()
		
		  }
		})
		// 获取页面视频 
		this.getVideoList()
	},
	getVideoList() {
		uni.request({
			url:this.baseUrl+`/api/themes?language=zh&per_page=12&page=${this.page}`,
			success:res=>{
				console.log(res)
				if(res.data.status==='1') {
					this.videoListArr = this.videoListArr.concat(res.data.data.list)
				}
			},
			fail:err=>{
				console.log(err)
			}
		})
	}
  }
}
